import { ReactNode, useState } from 'react';
import { WorkerGrid } from '../utils/grid/WorkerGrid';
import { useRefEffect } from '../utils/useRefEffect';
import { PositionedCell } from '../utils/grid/IGridCell';

export function GridRender({ gridRef, gridNeedsUpdate, afterUpdate, children = null }: { gridRef: React.MutableRefObject<WorkerGrid<string>>; gridNeedsUpdate: boolean; afterUpdate?(): void; children?: ReactNode; }) {
  const [cells, setCells] = useState<PositionedCell<string>[]>([]);
  const [isLoaded, setLoaded] = useState(gridNeedsUpdate);
  useRefEffect(([grid]) => {
    if (!gridNeedsUpdate && !grid.isWaitingForResize && isLoaded) {
      if (cells.length === grid.length)
        return;
    }
    let isUnmounted = false;
    grid.waitForArray().then(c => {
      if (isUnmounted)
        return;
      if (c.length !== cells.length) {
        setCells(c);
        setLoaded(true);
        afterUpdate && afterUpdate();
      }
    });
    return () => {
      isUnmounted = true;
    };
  }, [gridRef], [gridRef.current.length, cells.length, isLoaded, gridNeedsUpdate]);

  return (
    <>
      {children}
    </>
  );
}
