import { useCallback, useMemo, useRef, useState } from 'react';
import { Grid } from "../utils/grid";
import { GridCellPosition } from '../utils/grid/Grid';
import { ImageMaterial } from './ImageMaterial';
import { useRefEffect } from '../utils/useRefEffect';
import type { Mesh } from 'three/src/objects/Mesh';
import { verticalSlice } from './verticalSlice';
import { GridGlobeGeometry } from './GridGlobeGeometry';

const checkRect = (r1?: GridCellPosition) => (r2: GridCellPosition) =>
  r1 &&
  r1[0] === r2[0] &&
  r1[1] === r2[1] &&
  r1[2] === r2[2] &&
  r1[3] === r2[3]
;

function CellInside({ gridRef, src, afterImageLoad }: { gridRef: React.MutableRefObject<Grid<string>>; src: string; afterImageLoad?(image: ImageData): void; }) {
  const [rect, setRect] = useState<GridCellPosition>();

  const rCheck = useMemo(() => checkRect(rect), [rect]);

  useRefEffect(([grid]) => {
    return grid.subscribeToCell(src)((r) => {
      if (rCheck(r)) return;

      setRect(r);
    });
  }, [gridRef], [src, rCheck]);

  const onLoad = useCallback((image: ImageData) => {
    if (rect) return;

    afterImageLoad && afterImageLoad(image);
  }, [afterImageLoad, rect]);

  const ref = useRef<Mesh>();

  return (
    <mesh
      ref={ref}
      userData={{ rect }}
      >
      <GridGlobeGeometry rect={rect} />
      <ImageMaterial
        src={src}
        onLoad={onLoad}
        />
    </mesh>
  );
}
export function GridGlobeCell({ gridRef, src, afterImageLoad }: { gridRef: React.MutableRefObject<Grid<string>>; src: string; afterImageLoad?(image: ImageData): void; }) {
  useRefEffect(([grid]) => {
    return () => grid.remove(src);
  }, [gridRef], [src]);
  const onLoad = useCallback((image: ImageData) => {
    const grid = gridRef.current;
    if (!grid) return;
    grid.push({ key: src, height: image.width, next: undefined });
    afterImageLoad && afterImageLoad(image);
  }, [afterImageLoad, gridRef, src]);

  return (
    <CellInside {...{ gridRef, src, afterImageLoad: onLoad }} />
  );
}
