import { useRef } from 'react';
import { vSlice } from './verticalSlice';
import { SphereGeometry } from 'three/src/geometries/Geometries';
import { useBufferHeight } from '../utils/useBufferHeight';
const GUTTER = 0.005;
const sliceX = vSlice('_0', '_2')(1, GUTTER);
const sliceY = vSlice('_1_4', '_1_2')(1, GUTTER);
export function GridGlobeGeometry({ rect = [0.5, 0.5, 0, 0] }) {
  const buffer = useBufferHeight();
  const [x, y, w, h] = rect;
  const [xD, wD] = sliceX(x, x + w);
  const [yD, hD] = sliceY(y, y + h);
  const ref = useRef<SphereGeometry>();
  return (
    <sphereGeometry
      args={[buffer.diag * 2.4, 8, 8, xD, wD - xD, yD, hD - yD]}
      ref={ref}
      >

    </sphereGeometry>
  );
}
