import { useCallback, useRef, useState } from 'react';
import { GridConstraints } from '../utils/grid';
import { Group } from 'three/src/objects/Group';
import { WorkerGrid } from '../utils/grid/WorkerGrid';
import { GridGlobeCell } from './GridGlobeCell';
import { GridRender } from './GridRender';
import { useRefEffect } from '../utils/useRefEffect';
import { GlobeProps } from '@anonymous.club/types';

export function GridGlobe(props: GlobeProps) {
  const { imgs, cols = 4 } = props;
  const gridRef = useRef(WorkerGrid.create<string>(GridConstraints.MAX_COLS, [cols]));
  // useRefEffect(([grid]) => grid.subscribeGlobal(console.log), [gridRef], []);
  const groupRef = useRef<Group>();
  const [gridNeedsUpdate, setGridUpdate] = useState(false);
  const markGridForUpdate = useCallback(() => setGridUpdate(true), []);
  useRefEffect(([grid]) => {
    grid.setContraint([cols]);
    grid.resize();
  }, [gridRef], [cols]);
  // useRefEffect(([grid]) => {

  //   grid.resize();
  // }, [gridRef], [cols]);
  return (
    <group
      ref={groupRef}
      scale-z={-1}
      >
      {/* <group>
              {imgs.map((src) => (
                <GridGlobeCell
                  afterImageLoad={markGridForUpdate}
                  gridRef={gridRef}
                  key={src}
                  src={src}
                  ></GridGlobeCell>
            ))}
            </group> */}
      <GridRender
        afterUpdate={useCallback(() => setGridUpdate(false), [])}
        gridNeedsUpdate={gridNeedsUpdate}
        gridRef={gridRef}
        >
        {imgs.map((src) => (
          <GridGlobeCell
            afterImageLoad={markGridForUpdate}
            gridRef={gridRef}
            key={`${gridRef.current.id}/${cols}/${src}`}
            src={src}
            ></GridGlobeCell>
          ))}
      </GridRender>
    </group>
  );
}
export default GridGlobe;
